import './App.css';
import Sketch from 'react-p5';
// import * as Tone from 'tone';
// import 'p5/lib/addons/p5.sound';
import 'p5/lib/addons/p5.sound';
import React from "react";
import './myShape';
// import * as Pitchfinder from "pitchfinder";
import myShape from "./myShape";

function App() {
    let mic, fft;
    let inFreq;
    let my_shape = [];
    let my_shapeB;


    const setup = (p5, canvasParentRef) => {
        p5.createCanvas(540, 540).parent(canvasParentRef);

        p5.getAudioContext().suspend();

        my_shapeB = new myShape(p5, p5.width / 2, p5.height / 2);
        //  constructor  error
        mic = new p5.constructor.AudioIn();
        mic.start();
        fft = new p5.constructor.FFT();
        mic.connect(fft);
        // p5.getAudioContext().resume();

    };

    const draw = p5 => {
        p5.background(0);

        let spectrum = fft.analyze();
        let centerFrequency = fft.getCentroid();

        inFreq = p5.map(centerFrequency, 20, 14000, -100, 15000);

        p5.push();
        my_shapeB.update(p5, inFreq);
        my_shapeB.show(p5, true);

        p5.pop();

        p5.push();
        p5.stroke(255);
        p5.strokeWeight(0.5);
        // let xPosition = 50;
        let lineSpace = 2;
        // for (let index = 0; index < 220; index++) {
        //     if (index % 2 === 0) {
        //         p5.line(xPosition, p5.height / 2, xPosition + lineSpace, p5.height / 2);
        //         p5.line(p5.width / 2, xPosition, p5.width / 2, xPosition + lineSpace);
        //     }
        //     xPosition += lineSpace;
        // }

        let minPosition = p5.width * 0.1;
        let maxPosition = p5.width - (p5.width * 0.1);

        let count = 0;
        for (let i = minPosition; i < maxPosition; i++) {
            if (count % 4 === 0) {
                p5.line(i, p5.height / 2, i + lineSpace, p5.height / 2);
                p5.line(p5.height / 2, i, p5.height / 2, i + lineSpace);
                //         p5.line(p5.width / 2, xPosition, p5.width / 2, xPosition + lineSpace);

            }
            count++;
        }


        p5.pop();

        p5.push();
        p5.fill(255);
        // p5.textSize(16);
        p5.textSize(0.0296296 * p5.width);
        // p5.text(p5.round(centerFrequency) + " Hz", 450, 510); // 540
        p5.text(p5.round(centerFrequency) + " Hz", 0.8181 * p5.width, 0.944 * p5.width);
        // p5.text(p5.getAudioContext().state + " Hz", 0.8181 * p5.width, 0.944 * p5.width);
        p5.pop();
    };

    const touchStarted = p5 => {

        console.log(55);
        let state = p5.getAudioContext().state;
        // if (state !== 'running') {

            // p5.getAudioContext().resume();
            // p5.userStartAudio();
        // }
        p5.userStartAudio();
        console.log(state)
        // p5.getAudioContext().resume();
    };

    const mousePressed = p5 => {
        console.log("mousePressed");
        p5.userStartAudio()
    }


    return (
        <div className="justify-center">
            {/*<h1 className="text-center">React and P5</h1>*/}
            <Sketch setup={setup} draw={draw} touchStarted={touchStarted} mousePressed={mousePressed}/>
            <div className="flex justify-center p-2">
                <button type="button"
                        className="flex text-gray-900 hover:text-white border border-gray-800 hover:bg-gray-900 focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-gray-600 dark:text-gray-400 dark:hover:text-white dark:hover:bg-gray-600 dark:focus:ring-gray-800">Dark
                </button>
            </div>
        </div>

    );

}


export default App;
